.snake-game {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 1400px;
  margin: 0 auto;
}

.game-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.score {
  font-size: 1.5rem;
  color: var(--text-primary-dark);
}

.App.light .score {
  color: var(--text-primary-light);
}

.game-board {
  min-width: 600px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  border: 2px solid var(--text-primary-dark);
  border-radius: 4px;
  background-color: var(--bg-secondary-dark);
  margin: 0;
}

.App.light .game-board {
  border-color: var(--text-primary-light);
  background-color: var(--bg-secondary-light);
}

.row {
  display: flex;
}

.cell {
  width: 30px;
  height: 30px;
  min-width: 30px;
  min-height: 30px;
  border: 1px solid rgba(100, 255, 218, 0.1);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Fira Code', monospace;
  font-size: 11px;
  font-weight: bold;
}

.App.light .cell {
  border-color: rgba(42, 90, 136, 0.1);
}

.snake {
  background-color: var(--text-primary-dark);
  border-radius: 4px;
}

.App.light .snake {
  background-color: var(--text-primary-light);
}

.food {
  background-color: #00b4d8;
  border-radius: 50%;
}

.start-message {
  color: var(--text-secondary-dark);
  font-size: 1.2rem;
  text-align: center;
}

.App.light .start-message {
  color: var(--text-secondary-light);
}

.game-over {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  color: var(--text-primary-dark);
}

.App.light .game-over {
  color: var(--text-primary-light);
}

.reset-button {
  padding: 0.5rem 1rem;
  background-color: var(--text-primary-dark);
  color: var(--bg-primary-dark);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'Fira Code', monospace;
  transition: all 0.3s ease;
}

.App.light .reset-button {
  background-color: var(--text-primary-light);
  color: var(--bg-primary-light);
}

.reset-button:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

.snake-text {
  color: var(--bg-primary-dark);
  z-index: 2;
}

.App.light .snake-text {
  color: var(--bg-primary-light);
}

.food-text {
  color: white;
  z-index: 2;
  font-size: 10px;
}

.snake-game.fullscreen {
  width: 100vw;
  height: 100vh;
  background: var(--bg-primary-dark);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.App.light .snake-game.fullscreen {
  background: var(--bg-primary-light);
}

.game-controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 800px;
  padding: 0 2rem;
  margin-bottom: 2rem;
}

.fullscreen-button {
  background: var(--text-primary-dark);
  color: var(--bg-primary-dark);
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.App.light .fullscreen-button {
  background: var(--text-primary-light);
  color: var(--bg-primary-light);
}

.fullscreen-button:hover {
  transform: scale(1.1);
}

.snake-game.fullscreen .game-board {
  width: 80vh;
  height: 80vh;
  max-width: 90vw;
  max-height: 90vw;
}

.snake-game.fullscreen .cell {
  width: calc(80vh / 20);
  height: calc(80vh / 20);
  max-width: calc(90vw / 20);
  max-height: calc(90vw / 20);
}

.name-input-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  padding: 2rem;
  background: var(--bg-secondary-dark);
  border-radius: 8px;
}

.App.light .name-input-form {
  background: var(--bg-secondary-light);
}

.name-input-form input {
  padding: 0.8rem 1rem;
  border: 2px solid var(--text-primary-dark);
  border-radius: 4px;
  background: transparent;
  color: var(--text-primary-dark);
  font-family: 'Fira Code', monospace;
  font-size: 1rem;
  width: 100%;
  max-width: 300px;
  transition: all 0.3s ease;
}

.App.light .name-input-form input {
  border-color: var(--text-primary-light);
  color: var(--text-primary-light);
}

.name-input-form input:focus {
  outline: none;
  box-shadow: 0 0 10px rgba(100, 255, 218, 0.3);
}

.App.light .name-input-form input:focus {
  box-shadow: 0 0 10px rgba(42, 90, 136, 0.3);
}

.name-input-form button {
  padding: 0.8rem 2rem;
  background: transparent;
  border: 2px solid var(--text-primary-dark);
  border-radius: 4px;
  color: var(--text-primary-dark);
  font-family: 'Fira Code', monospace;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.App.light .name-input-form button {
  border-color: var(--text-primary-light);
  color: var(--text-primary-light);
}

.name-input-form button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 150%;
  height: 150%;
  background: var(--text-primary-dark);
  transform: translate(-50%, -50%) rotate(45deg) translateY(100%);
  transition: all 0.6s ease;
  z-index: -1;
}

.App.light .name-input-form button::before {
  background: var(--text-primary-light);
}

.name-input-form button:hover {
  color: var(--bg-primary-dark);
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(100, 255, 218, 0.3);
}

.App.light .name-input-form button:hover {
  color: var(--bg-primary-light);
  box-shadow: 0 5px 15px rgba(42, 90, 136, 0.3);
}

.name-input-form button:hover::before {
  transform: translate(-50%, -50%) rotate(45deg) translateY(0);
}

.name-input-form button:active {
  transform: translateY(-1px);
}

.game-container {
  display: flex;
  gap: 3rem;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
}

.snake-game.fullscreen .game-container {
  justify-content: center;
}

.high-scores {
  min-width: 250px;
  flex-shrink: 0;
}

.start-prompt {
  text-align: center;
  padding: 2rem;
}

.start-prompt h3 {
  margin-bottom: 1rem;
  color: var(--text-primary-dark);
}

.App.light .start-prompt h3 {
  color: var(--text-primary-light);
}

.start-prompt p {
  margin-bottom: 2rem;
  color: var(--text-secondary-dark);
}

.App.light .start-prompt p {
  color: var(--text-secondary-light);
} 